.modal {
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  /* padding-top: 20px;
  padding-bottom: 40px; */
  z-index: 100;

  display: flex;
  align-items: center;
  justify-content: center;

  background-color: rgba(18, 20, 23, 0.5);
}

.modalContent {
  box-sizing: border-box;
  position: relative;
  padding: 40px;
  border-radius: 12px;
  background-color: #ffffff;
  width: 541px;
  max-height: 80vh;
  overflow: auto;
}

.icon {
  position: absolute;
  z-index: 110;
  top: 16px;
  right: 16px;

  fill: #121417;
  cursor: pointer;
}

.thumb {
  width: 100%;
  height: 248px;
  margin-bottom: 14px;
}

.img {
  display: block;
  height: 100%;
  width: 100%;
  border-radius: 14px;
  object-fit: cover;
}

.tittleCard {
  margin-bottom: 8px;

  font-size: 18px;
  font-weight: 500;
  line-height: 1.3;
  color: #121417;
}

.modelCard {
  color: #3470ff;
}

.infoBox {
  width: 277px;
  margin-bottom: 14px;

  font-size: 12px;
  line-height: 1.5;
  color: rgba(18, 20, 23, 0.5);
}

.accessories {
  width: 100%;
  margin-bottom: 4px;
}

.functionalities {
  width: 100%;
  margin-bottom: 24px;
}

.description {
  width: 100%;
  margin-bottom: 24px;

  color: #121417;
  /* font-family: Manrope; */
  font-size: 14px;
  line-height: 1.43; /* 142.857% */
}

.detail {
  margin-bottom: 8px;

  color: #121417;
  /* font-family: Manrope; */
  font-size: 14px;
  line-height: 1.43; /* 142.857% */
}

.boxCondition {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin-bottom: 24px;
}

.condition {
  padding: 7px 14px;
  border-radius: 35px;
  background: #f9f9f9;

  color: #363535;
  /* font-family: Manrope; */
  font-size: 12px;
  line-height: 1.5;
}

.value {
  color: #3470ff;
  /* font-family: Montserrat; */
  font-size: 12px;
  font-weight: 600;
  line-height: 1.5;
  letter-spacing: -0.24px;
}

.linkTel {
  display: block;
  width: 168px;
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: center;
  border-radius: 12px;
  background-color: #3470ff;

  color: #ffffff;
  /* font-family: Manrope; */
  font-size: 14px;
  font-weight: 600;
  line-height: 1.43; /* 142.857% */
}

.linkTel:hover, .linkTel:focus {
  background-color: #0B44CD;;
}