.tittle {
  text-align: center;
  font-size: 46px;
}

.list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}

.item {
  width: 376px;
}

.link {
  display: block;
  border: none;
  background-color: transparent;
  transition: var(--shadow-transition);
}

.list .link:hover,
.list .link:focus {
  -webkit-box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12),
    0px 4px 4px rgba(0, 0, 0, 0.06), 1px 4px 6px rgba(0, 0, 0, 0.16);
  box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12), 0px 4px 4px rgba(0, 0, 0, 0.06),
    1px 4px 6px rgba(0, 0, 0, 0.16);
}

.thumb {
  position: relative;
  width: 100%;
  height: 370px;
}

.img {
  display: block;
  height: 100%;
  width: 100%;
  border-radius: 14px;
  object-fit: cover;
}

.content {
  padding-top: 25px;
  padding-left: 24px;
  padding-right: 24px;
  padding-bottom: 20px;
  border-bottom: 1px solid #eeeeee;
}

.item .itemTitle {
  margin-bottom: 4px;
  text-align: center;
  font-weight: 700;
  font-size: 20px;
  line-height: 2;
  letter-spacing: 0.06em;
  color: black;
}
